import React, { Component } from 'react';
import axios from 'axios';

const DB_URL = "http://localhost:4000";

export default class EditUser extends Component {
    
    constructor(props) {
        super(props);

        this.onChangeEmail = this.onChangeEmail.bind(this);
        this.onChangePassword = this.onChangePassword.bind(this);
        this.onChangeGender = this.onChangeGender.bind(this);
        this.onSubmit = this.onSubmit.bind(this);

        this.state = {
            email: '',
            password: '',
            gender: ''
        }
    }
    componentDidMount() {
        axios.get(DB_URL + '/users/' + this.props.match.params.id)
            .then(response => {
                this.setState({
                    email: response.data.email,
                    password: response.data.password,
                    gender: response.data.gender,
                })
            })
            .catch(function (error) {
                console.log(error);
            })
    }
    onChangeEmail(e) {
        this.setState({
            email: e.target.value
        });
    }

    onChangePassword(e) {
        this.setState({
            password: e.target.value
        });
    }

    onChangeGender(e) {
        this.setState({
            gender: e.target.value
        });
    }
    onSubmit(e) {
        e.preventDefault();
        const obj = {
            email: this.state.email,
            password: this.state.password,
            gender: this.state.gender,
        };
        console.log(obj);
        axios.post(DB_URL + '/users/edit/'+this.props.match.params.id, obj)
            .then(res => console.log(res.data));
        
        this.props.history.push('/admin');
        window.location.reload(true);
    }
    render() {
        return (
            <div className="navbar-form col-4 center-block">
                <h3 align="center">მომხმარებლის რედაქტირება</h3>
                <form onSubmit={this.onSubmit}>
                    <div className="form-group">
                        <label>იმეილი: </label>
                        <input type="text"
                            className="form-control"
                            value={this.state.email}
                            onChange={this.onChangeEmail}
                        />
                    </div>
                    <div className="form-group">
                        <label>პაროლი: </label>
                        <input
                            type="password"
                            className="form-control"
                            value={this.state.password}
                            onChange={this.onChangePassword}
                        />
                    </div>
                    <div className="form-group">
                        <div className="form-check form-check-inline">
                            <input className="form-check-input"
                                type="radio"
                                name="genderOptions"
                                id="Male"
                                value="ბიჭი"
                                checked={this.state.gender === 'ბიჭი'}
                                onChange={this.onChangeGender}
                            />
                            <label className="form-check-label">ბიჭი</label>
                        </div>
                        <div className="form-check form-check-inline">
                            <input className="form-check-input"
                                type="radio"
                                name="genderOptions"
                                id="Female"
                                value="გოგო"
                                checked={this.state.gender === 'გოგო'}
                                onChange={this.onChangeGender}
                            />
                            <label className="form-check-label">გოგო</label>
                        </div>
                        <div className="form-check form-check-inline">
                            <input className="form-check-input"
                                type="radio"
                                name="genderOptions"
                                id="notSure"
                                value="არ მიფიქრია"
                                checked={this.state.gender === 'არ მიფიქრია'}
                                onChange={this.onChangeGender}
                            />
                            <label className="form-check-label">არ მიფიქრია</label>
                        </div>
                    </div>
                    <br />

                    <div className="form-group">
                        <input type="submit" value="რედაქტირება" className="btn btn-primary" />
                    </div>
                </form>
            </div>
        )
    }
}