import React, { Component } from 'react';
import axios from 'axios';
import { Link } from 'react-router-dom';
import {
     Col, Form,
     Button,
} from 'react-bootstrap';

const DB_URL = "http://localhost:4000";

export default class SignUp extends Component {
     constructor(props) {
          super(props);
          this.state = {users: []};

          this.onChangeEmail = this.onChangeEmail.bind(this);
          this.onChangePassword = this.onChangePassword.bind(this);
          this.onChangeGender = this.onChangeGender.bind(this);
          this.onSubmit = this.onSubmit.bind(this);

          this.state = {
               email: '',
               password: '',
               gender: ''
          }
     }

     onChangeEmail(e) {
          this.setState({
               email: e.target.value
          });
     }

     onChangePassword(e) {
          this.setState({
               password: e.target.value
          });
     }

     onChangeGender(e) {
          this.setState({
               gender: e.target.value
          });
     }

     onSubmit(e) {
          e.preventDefault();

          console.log(`დარეგისტრირდი:`);
          console.log(`იმეილი: ${this.state.email}`);
          console.log(`პაროლი: ${this.state.password}`);
          console.log(`სქესი: ${this.state.gender}`);

          const newUser = {
               email: this.state.email,
               password: this.state.password,
               gender: this.state.gender
          };

          axios.post(DB_URL + '/users/register', newUser)
          .then((res) => console.log(res))
          .catch((error) => console.log(error));


          this.setState({
               email: '',
               password: '',
               gender: ''
          })
     }
     render() {
          document.body.style.color = "white";
          return (
               <div>
                    <div id='signupContainer'>
                         <center>
                              <div style={{ width: 600, textAlign: 'left', padding: 10, marginTop: 10 }} className="block-example border border-primary rounded mb-0">
                                   <Form onSubmit={this.onSubmit}>
                                        <Form.Group as={Form.Row} controlId="formHorizontalEmail">
                                             <Form.Label column sm={2}>
                                                  იმეილი
                                        </Form.Label>
                                             <Col sm={10}>
                                                  <Form.Control type="email" required value={this.state.email} onChange={this.onChangeEmail} placeholder="სახელი@დომეინი.ge" />
                                             </Col>
                                        </Form.Group>

                                        <Form.Group as={Form.Row} controlId="formHorizontalPassword">
                                             <Form.Label column sm={2}>
                                                  პაროლი
                                        </Form.Label>
                                             <Col sm={10}>
                                                  <Form.Control type="password" required value={this.state.password} onChange={this.onChangePassword} placeholder="შეიყვანე პაროლი" />
                                             </Col>
                                        </Form.Group>
                                        <fieldset>
                                             <Form.Group as={Form.Row}>
                                                  <Form.Label as="legend" column sm={2}>
                                                       სქესი:
                                             </Form.Label>
                                                  <Col sm={10}>
                                                       <Form.Check
                                                            type="radio"
                                                            label="ბიჭი"
                                                            name="genderOptions"
                                                            id="genderMale"
                                                            onChange={this.onChangeGender}
                                                            value="ბიჭი"
                                                            required
                                                       />
                                                       <Form.Check
                                                            type="radio"
                                                            label="გოგო"
                                                            name="genderOptions"
                                                            id="genderFemale"
                                                            onChange={this.onChangeGender}
                                                            value="გოგო"
                                                            required
                                                       />
                                                       <Form.Check
                                                            type="radio"
                                                            label="არ მიფიქრია"
                                                            name="genderOptions"
                                                            id="notSure"
                                                            onChange={this.onChangeGender}
                                                            value="არ მიფიქრია"
                                                            required
                                                       />
                                                  </Col>
                                             </Form.Group>
                                        </fieldset>
                                        <Form.Group as={Form.Row} controlId="formHorizontalCheck">
                                             <Col sm={{ span: 10, offset: 2 }}>
                                                  <Form.Check label="მაგარი საიტია" />
                                             </Col>
                                        </Form.Group>

                                        <Form.Group as={Form.Row}>
                                             <Col sm={{ span: 10, offset: 2 }}>
                                                  <Button type="submit" value="Register">დარეგისტრირება</Button>
                                                  <Link to="/"><Button style={{ marginLeft: 20 }}>დაბრუნება</Button></Link>
                                             </Col>
                                        </Form.Group>
                                   </Form>
                              </div>
                         </center>
                    </div>
               </div>
          )
     }
}