import React, { Component } from 'react';
import axios from 'axios';
import { Link } from 'react-router-dom';

const DB_URL = "http://localhost:4000";

const User = props => (
    <tr>
        <td className={props.user.gender==="ბიჭი" ? 'bichi' : 'ara-bichi'}>{props.user.email}</td>
        <td className={props.user.gender==="ბიჭი" ? 'bichi' : 'ara-bichi'}>{props.user.password}</td>
        <td className={props.user.gender==="ბიჭი" ? 'bichi' : 'ara-bichi'}>{props.user.gender}</td>
        <td>
            <Link to={"/users/"+props.user._id}>რედაქტირება</Link>
        </td>
    </tr>
)

export default class Admin extends Component {
    constructor(props) {
        super(props);
        this.state = { users: [] };
    }
    componentDidMount() {
        axios.get(DB_URL + '/users/')
            .then(response => {
                this.setState({ users: response.data });
            })
            .catch(function (error) {
                console.log(error);
            })
    }
    userList() {
        return this.state.users.map(function(currentUser, i){
            return <User user={currentUser} key={i} />;
        })
    }
    render() {
        return (
            <div>
                <h3>მომხმარებლების სია</h3>
                <table className="table table-striped" style={{ marginTop: 20 }} >
                    <thead>
                        <tr>
                            <th>იმეილი</th>
                            <th>პაროლი</th>
                            <th>სქესი</th>
                        </tr>
                    </thead>
                    <tbody>
                        {this.userList()}
                    </tbody>
                </table>
            </div>
        )
    }
}


