import React, { Component } from "react";
import PropTypes from "prop-types";
import DisplayChild from "./display-child.component";

class Display extends Component {
  state = {
    APR: 0
  };

  componentDidMount() {
    this.calculateAPR();
  }

  componentDidUpdate(prevProps) {
    if (prevProps !== this.props) {
      this.calculateAPR();
    }
  }

  calculateAPR = () => {
    const { amount, years } = this.props;

    const sulXinklebi = amount;
    const sulKababi = years;
    const gadasaxadi = sulXinklebi * 0.8 + sulKababi * 12;

    if (1 < gadasaxadi && gadasaxadi < 20) {
      this.setState({ APR: 0.05 });
    }
    if (20 < gadasaxadi && gadasaxadi < 40) {
      this.setState({ APR: 0.1 });
    }
    if (40 < gadasaxadi && gadasaxadi < 60) {
      this.setState({ APR: 0.15 });
    }
    if (60 < gadasaxadi && gadasaxadi < 99) {
      this.setState({ APR: 0.2 });
    }
    if (1 > gadasaxadi) {
      this.setState({ APR: 0 });
    }
  };

  calculateMonthlyRepayment = () => {
    const { amount, years } = this.props;

    // const decimalFormat = this.state.APR + 1;
    // const totalOwed = decimalFormat * amount;
    const sulXinklebi = amount;
    const sulKababi = years;
    const gadasaxadi = sulXinklebi * 0.8 + sulKababi * 12;

    return <strong><p className="calcp">{Math.round(gadasaxadi)} ლარი</p></strong>;
  };

  percentageAPR = () => {
    return <p className="calcp">{this.state.APR * 100}%</p>;
  };

  render() {
    return (
      <div className="flex">
        <DisplayChild func={this.percentageAPR()} text="+საკომისიო" />
        <DisplayChild
          func={this.calculateMonthlyRepayment()}
          text=" სულ გადასახდელი"
        />
      </div>
    );
  }
}

Display.propTypes = {
  years: PropTypes.number.isRequired,
  amount: PropTypes.number.isRequired
};

export default Display;