import React from 'react';
import logo from '../../logo.svg';
import '../../App.css';
import { Link } from 'react-router-dom';

var clicks = 0;

class Clock extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      time: new Date().toLocaleString()
    };
  }
  componentDidMount() {
    this.intervalID = setInterval(
      () => this.tick(),
      500
    );
  }
  componentWillUnmount() {
    clearInterval(this.intervalID);
  }
  tick() {
    this.setState({
      time: new Date().toLocaleString()
    });
  }
  render() {
    if (clicks === 13) {
      return (
        <p className="App-clock">
          ამ დროსაც ყლე ხარ: {this.state.time}.
      </p>
      );
    } else return null;
  }
}

function FunctionalCounter() {
  const [value, setValue] = React.useState(0);
  return (<div>
    <span>გაიგე რამდენად ყლე ხარ: {value}  </span>
    <button style={{ backgroundColor: 'black', color: 'white', height: 60 }} onClick={() => {
      setValue(oldValue => oldValue + 1);
      if (value === 6) {
        alert('როდემდე აპირებ გაგრძელებას..');
      } else if (value === 13) {
        clicks = 13;
      }
    }}>დააჭირე</button>
  </div>);
};

function Home() {
  return (
    <div className="App">
      <header className="App-header">
        <img src={logo} className="App-logo" alt="logo" />
        <br />
        <p>
          Welcome to Resto
        </p>
        <Link
          className="App-link"
          to="/"
        >
          Resto.ge © 2019
        </Link>
      </header>
      <div className="App-body">
        <br />
        <FunctionalCounter />
        <Clock />
        <br />
        <Link to="/registracia">დარეგისტრირდი</Link>
      </div>
    </div>
  );
}

export default Home;